"use client";

import React, { useContext } from "react";
import Image from "next/image";
import Link from "next/link";
import StoreContext from "../context/StoreContext";
import { Trash2, Plus, Minus } from "lucide-react";

export default function Cart() {
  const { state, dispatch } = useContext(StoreContext);
  const { cart } = state;

  const updateQuantity = (id, newQuantity) => {
    if (newQuantity <= 0) {
      dispatch({
        type: 'REMOVE_FROM_CART',
        payload: id
      });
    } else {
      dispatch({
        type: 'UPDATE_CART_QUANTITY',
        payload: { id, quantity: newQuantity }
      });
    }
  };

  const removeFromCart = (id) => {
    dispatch({
      type: 'REMOVE_FROM_CART',
      payload: id
    });
  };

  const getTotalPrice = () => {
    return cart.reduce((total, item) => total + (item.price * item.quantity), 0).toFixed(2);
  };

  const getTotalItems = () => {
    return cart.reduce((total, item) => total + item.quantity, 0);
  };

  if (cart.length === 0) {
    return (
      <div className="text-center py-12">
        <div className="text-6xl mb-4">🛒</div>
        <h3 className="text-xl font-semibold text-gray-900 dark:text-white mb-2">
          Your cart is empty
        </h3>
        <p className="text-gray-600 dark:text-gray-400 mb-6">
          Add some products to your cart to get started
        </p>
        <Link
          href="/products"
          className="bg-indigo-600 text-white px-6 py-3 rounded-lg hover:bg-indigo-700 transition-colors"
        >
          Continue Shopping
        </Link>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Cart Items */}
      <div className="space-y-4">
        {cart.map((item) => (
          <div
            key={`${item.id}-${item.size}-${item.color}`}
            className="bg-white dark:bg-gray-900 border border-gray-200 dark:border-gray-700 rounded-lg p-4 flex flex-col sm:flex-row gap-4"
          >
            {/* Product Image */}
            <div className="flex-shrink-0">
              <Image
                src={item.image}
                alt={item.name}
                width={100}
                height={100}
                className="w-24 h-24 object-cover rounded-md"
              />
            </div>

            {/* Product Info */}
            <div className="flex-1 space-y-2">
              <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
                {item.name}
              </h3>
              <div className="flex flex-wrap gap-2 text-sm text-gray-600 dark:text-gray-400">
                <span>Size: {item.size}</span>
                <span>•</span>
                <span>Color: {item.color}</span>
              </div>
              <div className="text-lg font-bold text-gray-900 dark:text-white">
                ${item.price}
              </div>
            </div>

            {/* Quantity Controls */}
            <div className="flex items-center space-x-3">
              <button
                onClick={() => updateQuantity(item.id, item.quantity - 1)}
                className="w-8 h-8 border border-gray-300 dark:border-gray-600 rounded-md flex items-center justify-center hover:bg-gray-50 dark:hover:bg-gray-800"
              >
                <Minus size={16} />
              </button>
              <span className="text-lg font-semibold text-gray-900 dark:text-white w-8 text-center">
                {item.quantity}
              </span>
              <button
                onClick={() => updateQuantity(item.id, item.quantity + 1)}
                className="w-8 h-8 border border-gray-300 dark:border-gray-600 rounded-md flex items-center justify-center hover:bg-gray-50 dark:hover:bg-gray-800"
              >
                <Plus size={16} />
              </button>
            </div>

            {/* Item Total */}
            <div className="flex flex-col items-end space-y-2">
              <div className="text-lg font-bold text-gray-900 dark:text-white">
                ${(item.price * item.quantity).toFixed(2)}
              </div>
              <button
                onClick={() => removeFromCart(item.id)}
                className="text-red-600 hover:text-red-800 dark:text-red-400 dark:hover:text-red-300 p-1"
                aria-label="Remove item"
              >
                <Trash2 size={16} />
              </button>
            </div>
          </div>
        ))}
      </div>

      {/* Cart Summary */}
      <div className="bg-gray-50 dark:bg-gray-800 rounded-lg p-6">
        <div className="space-y-3">
          <div className="flex justify-between text-gray-600 dark:text-gray-400">
            <span>Items ({getTotalItems()})</span>
            <span>${getTotalPrice()}</span>
          </div>
          <div className="flex justify-between text-gray-600 dark:text-gray-400">
            <span>Shipping</span>
            <span>Free</span>
          </div>
          <div className="border-t border-gray-200 dark:border-gray-600 pt-3">
            <div className="flex justify-between text-lg font-bold text-gray-900 dark:text-white">
              <span>Total</span>
              <span>${getTotalPrice()}</span>
            </div>
          </div>
        </div>

        <div className="mt-6 space-y-3">
          <Link
            href="/checkout"
            className="w-full bg-black dark:bg-white text-white dark:text-black py-3 rounded-lg font-semibold hover:bg-gray-800 dark:hover:bg-gray-200 transition-colors text-center block"
          >
            Proceed to Checkout
          </Link>
          <Link
            href="/products"
            className="w-full border border-gray-300 dark:border-gray-600 text-gray-700 dark:text-gray-300 py-3 rounded-lg font-semibold hover:bg-gray-50 dark:hover:bg-gray-800 transition-colors text-center block"
          >
            Continue Shopping
          </Link>
        </div>
      </div>
    </div>
  );
}
